.table {
    display: table;
    width: 100%;
    .table-row {
        display: table-row;
        .table-cell {
            display: table-cell;
        }
    }
}

.table-hover {
    tbody {
        tr {
            &:hover {
                background-color: rgba(0, 0, 0, .075);
            }
        }
    }
}

.table-striped {
    tbody {
        tr {
            &:nth-of-type(odd) {
                background-color: rgba(0, 0, 0, .05);
            }
        }
    }
}

table {
    width: 100%;
    td, 
    th {
        text-align: center;
        &.left {
            text-align: left;
        }
        &.right {
            text-align: right;
        }
    }
    thead {
        input,
        select {
            width: 100%;
            border: none;
            border-bottom: 1px solid #555;
            height: $lineHeight;
        }
        .sort-desc {
            &::after {
                font-family: "Font Awesome 5 Free";
                font-size: inherit;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                content: "\f15e";
            }
        }
        .sort-asc {
            &::after {
                font-family: "Font Awesome 5 Free";
                font-size: inherit;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                content: "\f15d";
            }
        }
    }
}